/**
 *  SendModal 
 * @param confirmationFn
 */
export const useSendModal = (confirmationFn:Function) => {
    const {onPresent} = useContext(ModalContext);
    const onWithdraw = useCallback((...res)=>{
        onPresent(<SendModal confirmationFn={() => confirmationFn(...res)}/>);
    },[])
    return onWithdraw;
}



