// @ts-ignore
import Pinata from "@pinata/sdk"
import env from "@beam-australia/react-env";



export class IpfsServer {
    public pinata: any;

    constructor() {
        this.pinata = Pinata(
            env('PINATA_API_KEY'),
            env('PINATA_SECRET_API_KEY'),
        );
    }


    /**
     *  address: body.address,
     msg: body.msg,
     sig: body.sig,
     version: '2'
     * @param body
     */


     public async pinJson(body: any) {
        let ipfsHash: any;

        try {
            const result = await this.pinata.pinJSONToIPFS(body);
            ipfsHash = result.IpfsHash;
            /*if (service === 'fleek') {
              const input = config;
              input.key = key;
              input.data = JSON.stringify(body);
              const result = await fleek.upload(input);
              ipfsHash = result.hashV0;
            } else {
              const result = await pinata.pinJSONToIPFS(body);
              ipfsHash = result.IpfsHash;
            }*/
        } catch (e) {
            console.log(e, '<------------------------ pinJSONToIPFS');
        }

        fetch(`https://ipfs2arweave.com/permapin/${ipfsHash}`)
            .then(res => res.json())
            .then(json => console.log('Arweave success', json))
            .catch(e => console.error('Arweave error', e));

        return ipfsHash;
    }
}
