import React, {useCallback, useContext} from "react";
import styled from "styled-components";
import {ButtonWidget, InputWidget, IsApprove, ListCellWidget, SpacingWidget} from "../../base";
import {SelectWidget} from "../average/select_widget";
import {USDT} from "../../../nuser/token.config";
