import {SuccessWidget} from "./success_widget";

export const DescriptionWidget: React.FC = () => {
    const {register, handleSubmit,  errors } = useForm();
    const [urlData] = UseUrlData<any>();
    const {onPresent} = useContext(ModalContext);



    const handleLogin = useCallback(async (inputData: any) => {
        if (urlData === undefined) return;
        try {
            onPresent(<AppleDescriptionModal
                CellModel={{...urlData,...inputData}}
            />)
        } catch (e) {
            console.log(e);
        }

    }, [urlData]);

    return (
        <DescriptionWapStyle>
            <form>
                <InputWidget

                    label={"Description"}
                    placeholder={"Please fill in a detailed description of your loss"}
                    errors={errors}
                    name={'description'}
                    multiline={true}
                    inputRef={register({
                        required: 'Please fill in a detailed description of your loss',
                        // min: {value: urlData.minDuration, message: `Min ${urlData.minDuration} days`},
                        // max: {value: urlData.maxDuration, message: `Max ${urlData.maxDuration} days`},
                    })}
                />

                <SpacingWidget/>

                <InputWidget
                    errors={errors}
                    inputRef={register({
                        required: 'no Enter the number is dame',
                        // validate: (ev) => {
                        //     if (!(ev.toString().split("."))[1] || ev.toString().split(".")[1].length < 2) {
                        //         return;
                        //     }
                        //     return "Must be a multiple of 0.1"
                        // }
                    })}
                    name={'credentials'}

                    label={"Credentials"}
                    type='text'
                    placeholder={""}
                >

                </InputWidget>


            </form>

            <ButtonWidget onClick={handleSubmit(handleLogin)} text="Apply" type="img"/>
            {/* <ButtonWidget onClick={handleSuccess} text="Apple" type="img"/> */}
        </DescriptionWapStyle>
    );
};


let DescriptionWapStyle = styled.div`
  background-color: ${({theme}) => theme.bgColor.main};
  height: 100%;
  padding: 40px 15px;
  box-sizing: border-box;

  form{
    .MuiInputBase-input{
      text-align: start;
    }
  }
  > div:last-child {
    margin-top: 40px;
  }
`
