import * as ethUtil from 'ethereumjs-util'
import * as sigUtil from 'eth-sig-util'
import {NetworkContextName, useWallet} from "../use_wallet";
import {useWeb3} from "../provider/web3_provider";
import {useCallback} from "react";
import {Web3Provider} from '@ethersproject/providers'
import {Web3ReactContextInterface} from "@web3-react/core/dist/types";
import {useWeb3React as useWeb3ReactCore} from '@web3-react/core'


export const useSignFn = (): (msgData: any) => Promise<string> => {
    const {account} = useWallet();
    const {walletConnect} = useWeb3()

    const {chainId, library} = useActiveWeb3React()

    const handleFn = useCallback(async (msgData: any) => {
        if (!account) return;
        let params = [account, msgData]
        if (walletConnect) {
            let data = await walletConnect.signTypedData(params);
            console.log('TYPED SIGNED:' + JSON.stringify(data))
            const recovered = sigUtil.recoverTypedSignature_v4({data: JSON.parse(msgData), sig: data})
            console.log(ethUtil.toChecksumAddress(recovered), account)
            return data;
        }
        let data = await sendAsync(library, params, account);
        return data;
    }, [library])

    return handleFn
}


async function sendAsync(web3: Web3Provider | undefined, params: any[], account: string): Promise<any> {
    let method = 'eth_signTypedData_v4'

    let result;
    try {
        if (!web3) return;
        result = await web3.send(method, params);
        console.log(result)

        console.log('TYPED SIGNED:' + JSON.stringify(result.result))
        const recovered = sigUtil.recoverTypedSignature_v4({data: JSON.parse(params[1]), sig: result.result})
        console.log(ethUtil.toChecksumAddress(recovered), account)
    } catch (e) {
        throw  e;
    }

    return result.result;

}


export const useLedgerSignFn = (): (msgData: any) => Promise<string> => {
    const {account} = useWallet();
    const activeWeb3React = useActiveWeb3React()
    const {library} = useWeb3()


    const handleFn = useCallback(async (msgData: any) => {
        if (!account) return;
        if (!library) throw new Error('missing dependencies');
        console.log('useLedgerSignFn----------------->',library,activeWeb3React);
        let method = 'eth_signTypedData_v4'
        const params = [account, msgData];
        let _hax;
        try {

            _hax = await library.send(method, [account, msgData]);
            console.log(_hax);
            alert('sign success:' + _hax);
            const recovered = sigUtil.recoverTypedSignature_v4({data: JSON.parse(params[1]), sig: _hax})
            console.log(ethUtil.toChecksumAddress(recovered) == account);
            alert(`sign verification:${ethUtil.toChecksumAddress(recovered) == account} 
            address: ${ethUtil.toChecksumAddress(recovered)}
            `);

        } catch (e) {
            if (e.code == 4001) {
                throw  e;
            }
            console.log(e);
            alert('sign fail')
        }

        return _hax;
    }, [library])

    return handleFn
}


export function useActiveWeb3React(): Web3ReactContextInterface<Web3Provider> {
    const context = useWeb3ReactCore<Web3Provider>()
    const contextNetwork = useWeb3ReactCore<Web3Provider>(NetworkContextName)

    return context.active ? context : contextNetwork
}





